import * as React from 'react';
import {
  Routes, Route, Navigate,
} from 'react-router-dom';
import AnonymousLayout from './layouts/AnonymousLayout';
import DesignPage from './pages/DesignPage';
import ImplementationPage from './pages/ImplementationPage';
import IntroPage from './pages/IntroPage';
import KinesiophobiaPage from './pages/KinesiophobiaPage';
import MembersPage from './pages/MembersPage';
import TechandequipmentPage from './pages/TechandequipmentPage';
import TestingPage from './pages/TestingPage';
import UIChangesPage from './pages/UIChangesPage';
import PurposePage from './pages/PurposePage';
import ResultsPage from './pages/ResultsPage';

export default function AppRoutes() {
  return (
    <div>
      <Routes>
        <Route path="/" element={<AnonymousLayout />}>
          <Route path="intro" element={<IntroPage />} />
          <Route path="members" element={<MembersPage />} />
          <Route path="kinesiophobia" element={<KinesiophobiaPage />} />
          <Route path="purpose" element={<PurposePage />} />
          <Route path="techandequipment" element={<TechandequipmentPage />} />
          <Route path="design" element={<DesignPage />} />
          <Route path="implementation" element={<ImplementationPage />} />
          <Route path="uichanges" element={<UIChangesPage />} />
          <Route path="testing" element={<TestingPage />} />
          <Route path="results" element={<ResultsPage />} />
          <Route
            path="*"
            element={<Navigate to="/" replace />}
          />
        </Route>
      </Routes>
    </div>
  );
}
