import {
  Outlet, useLocation, useNavigate,
} from 'react-router-dom';
import CssBaseline from '@mui/material/CssBaseline';
import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { ChevronRight } from '@mui/icons-material';
import { has } from 'lodash';
import { useEffect } from 'react';

const drawerWidth = 240;

const headerAliases : { [key:string]:string } = {
  intro: 'Welcome',
  members: 'Who are we?',
  kinesiophobia: 'What is kinesiophobia?',
  purpose: 'Aim of this project',
  techandequipment: 'Techologies and Equipment',
  design: 'Our Design',
  uichanges: 'Changes and UI Considerations',
  implementation: 'Implementation and Process',
  testing: 'Testing and Debugging',
  results: 'Results and Discussion',
};

export default function AnonymousLayout() {
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    if (!has(Object.keys(headerAliases), location.pathname.split('/').slice(-1)[0])) {
      navigate('/intro');
    }
  }, []);

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{ width: `calc(100% - ${drawerWidth}px)`, ml: `${drawerWidth}px` }}
      >
        <Toolbar>
          <Typography variant="h6" noWrap component="div">
            {headerAliases[location.pathname.split('/').slice(-1)[0]]}
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="permanent"
        anchor="left"
      >
        <Toolbar />
        <Divider />
        <List>
          {Object.keys(headerAliases).map((key) => (
            <ListItem key={key} disablePadding>
              <ListItemButton
                onClick={() => {
                  navigate(key);
                }}
                disabled={false}
              >
                <ListItemIcon>
                  <ChevronRight />
                </ListItemIcon>
                <ListItemText primary={headerAliases[key]} />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{ flexGrow: 1, bgcolor: 'background.default', p: 3 }}
      >
        <Toolbar />
        <Outlet />
      </Box>
    </Box>
  );
}
